.footer {
  background-color: #1a1a1a;
  color: #d6d0d0;
  text-align: center;
  padding: 20px 0;
  bottom: 0;
  width: 100%;
}

.footer-container {
  max-width: 1100px;
  margin: auto;
  padding: 0 20px;
}

.social-links {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 15px;
}

.social-links a {
  color: #d6d0d0;
  text-decoration: none;
  font-size: 18px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.social-links a:hover {
  color: #6a0dad;
}

.icon {
  font-size: 20px;
}

.footer-nav {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 15px;
}

.footer-nav li {
  display: inline;
}

.footer-nav a {
  color: #d6d0d0;
  text-decoration: none;
  font-size: 16px;
}

.footer-nav a:hover {
  color: #6a0dad;
}

.copyright {
  font-size: 14px;
  margin-top: 10px;
  opacity: 0.8;
}

/* Responsividade para celulares e tablets */
@media (max-width: 1024px) {
  .header, .footer, .about-container, .projects-container, .contact-container {
    padding: 1rem; /* Reduz o padding para ajustar melhor em telas menores */
  }

  .navbar {
    flex-direction: column; /* Links em coluna em telas menores */
    gap: 1rem; /* Aumenta o espaçamento entre os links */
    text-align: center; /* Centraliza os links */
  }

  .logo img {
    height: 150px; /* Ajusta a logo para tamanhos menores */
  }

  .cta-button .button {
    padding: 0.8rem 1.2rem; /* Ajuste no tamanho do botão */
    font-size: 14px; /* Ajuste no tamanho da fonte */
  }

  .social-links {
    flex-direction: column; /* Links sociais em coluna */
    gap: 10px;
  }

  .footer-nav {
    flex-direction: column; /* Itens de navegação no footer em coluna */
    gap: 10px;
  }

  .about-photo, .about-text {
    width: 100%; /* Garante que os itens ocuparam 100% da largura */
  }

  .project-card {
    width: 90%; /* Ajusta os cards para maior aproveitamento de espaço */
  }

  .video-container {
    width: 100%;
  }

  /* Ajuste nas imagens e vídeos */
  .about-video {
    width: 100%;
    max-width: 500px; /* Limita o tamanho máximo da imagem/vídeo */
    height: auto;
  }

  .contact-form input,
  .contact-form textarea {
    font-size: 14px; /* Ajuste no tamanho da fonte */
    padding: 10px;
  }

  /* Ajuste na exibição de vídeos em containers */
  .project-video {
    width: 100%; /* Garante que o vídeo ocupe toda a largura disponível */
  }

  .project-card {
    width: 100%; /* Ajusta a largura dos cards para dispositivos menores */
  }
}

/* Responsividade para celulares menores (max-width: 480px) */
@media (max-width: 480px) {
  .header {
    flex-direction: column; /* Alinha os itens na vertical */
    padding: 0.5rem 1rem; /* Reduz o padding */
  }

  .logo img {
    height: 120px; /* Ajuste ainda maior para a logo */
  }

  .navbar {
    gap: 0.8rem; /* Reduz o espaçamento entre os links */
  }

  .cta-button a {
    padding: 8px 12px;
    font-size: 12px; /* Ajuste no tamanho do botão */
  }

  .project-card {
    width: 100%; /* Ajusta o card para ocupar toda a largura */
  }

  .footer-nav {
    gap: 8px;
  }

  .contact-container {
    width: 100%; /* Garante que o formulário ocupe toda a largura */
  }

  .contact-form input,
  .contact-form textarea {
    font-size: 14px; /* Ajuste na fonte */
    padding: 10px;
  }

  .footer-container {
    padding: 0 10px;
  }

  /* Ajustes de vídeos e fotos */
  .about-photo {
    width: 80%;
  }

  .about-text h1 {
    font-size: 24px; /* Reduz o título em telas pequenas */
  }

  .about-text p {
    font-size: 14px; /* Reduz o parágrafo */
  }

  .projects-container h1 {
    font-size: 28px; /* Reduz o tamanho do título de projetos */
  }

  .projects-scroll {
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }
}

/* Responsividade para dispositivos extra pequenos */
@media (max-width: 360px) {
  .logo img {
    height: 80px; /* Reduz logo em telas menores */
  }

  .cta-button .button {
    padding: 0.5rem 1rem;
    font-size: 12px; /* Ajuste no botão */
  }

  .social-links a {
    font-size: 14px; /* Reduz tamanho do ícone */
  }

  .footer-container, .contact-container {
    padding: 0 10px;
  }

  .contact-form input, .contact-form textarea {
    font-size: 12px;
    padding: 8px;
  }
}

